import React from "react";
import Aan from "../../assets/images/aan2.png";
import scrollTo from "gatsby-plugin-smoothscroll";

function AboutSection() {
  return (
    <>
      <div className="site-section" id="about-section">
        <div className="container">
          <div className="row">
            <div className="col-12 mb-4 position-relative">
              <h2
                className="section-title"
                data-sal="fade"
                data-sal-delay="1500"
                data-sal-easing="ease"
              >
                About Me
              </h2>
            </div>
            <div className="col-lg-4 order-1 order-lg-2 mb-4 mb-lg-0">
              <div
                data-sal="fade"
                data-sal-delay="1500"
                data-sal-easing="ease"
              >
                <img
                  src={Aan}
                  alt="Images"
                  className="img-fluid"
                  style={{ margintop: "-150px" }}
                />
              </div>
            </div>
            <div className="col-lg-4 order-2 order-lg-1">
              <p
                data-sal="slide-left"
                data-sal-delay="1500"
                data-sal-easing="ease"
              >
                Hello. I'm Mochamad Farchan Ardiatama, known professionally as Aan, Possessing a Bachelor's degree in Information Technology with a GPA of 3.87/4.00, a year of experience as a Full Stack Developer at Pertamina Lubricants, and currently engaged as an Associate Software Developer at Deloitte, I specialize in a range of advanced technologies.
              </p>
              <p
                data-sal="slide-left"
                data-sal-delay="2000"
                data-sal-easing="ease"
              >
                Across various domains, my expertise is applied, with a focus on Microsoft Azure, Cybersecurity (including DAST, SAST, and Pentest methodologies), Asp.net, Angular, RPA, Blazor, and .Net frameworks. At Deloitte, I am continually challenged and supported to develop robust solutions and navigate complex technical landscapes.
              </p>
            </div>
            <div className="col-lg-4 order-3 order-lg-3">
              <p
                data-sal="slide-right"
                data-sal-delay="2500"
                data-sal-easing="ease"
              >
                Driven by a relentless pursuit of excellence, dynamic environments are where I thrive. Staying abreast of industry trends and embracing emerging technologies ensures that I remain at the forefront of the ever-evolving IT landscape.
              </p>
              <p
                data-sal="slide-right"
                data-sal-delay="3000"
                data-sal-easing="ease"
              >
                Effectively contributing to high-impact projects, leveraging a blend of academic rigor and practical experience, is my commitment. Success is driven and strategic objectives are propelled forward through my contributions.
              </p>
              <p
                data-sal="slide-up"
                data-sal-delay="2500"
                data-sal-easing="ease"
              >
                <a
                  className="btn btn-primary wow fadeInRight active"
                  data-wow-delay="1s"
                  style={{ cursor: "pointer" }}
                  onClick={() => scrollTo("#Experience-section")}
                >
                  My Experience
                </a>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default AboutSection;
